		width: 100%;
		height: 94%;
		padding: rem(21) rem(12);
		align-items: flex-start;
		justify-content: flex-start;
	}

	&.show {
		display: flex;
	}

	&__title {
		font-size: rem(14);
		font-weight: 400;
		color: $colorBlueDark;

		@media(min-width: 992px) {
			text-align: center;
		}

		@media(max-width: 991px) {
		    transform: translateY(16px);
		}
	}

	&__btns {
		width: 100%;
		display: grid;
		grid-template-columns: repeat(2, 1fr);
		gap: rem(20);
		margin: rem(57) 0 0;

		@media(min-width: 640px) {
			max-width: rem(292);
			gap: rem(10);
			margin: 0 auto;
		}
	}

	&__yes {
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		font-family: $secFont;
		font-size: rem(14);
		color: #4CAF50;
		width: 100%;
		height: rem(33);
		border-radius: rem(12);
		border: 1px solid #4caf50;
		background-color: #fff;
		transition: background-color 0.2s ease, color 0.2s ease;
		position: relative;

		&::after {
			content: '';
			display: block;
			width: 100%;
			height: 100%;
			position: absolute;
			border-radius: inherit;
			top: 0;
			left: 0;
			z-index: 1;
			border: 1px solid $colorGreenLight;
			box-shadow: 0 rem(2) rem(4) rgba(#4CAF50, 0.4);
			opacity: 0;
			transition: opacity 0.2s ease;
		}

		&:hover {
			border-color: $colorGreenDark;
			color: $colorGreenDark;

			.mid-btn__text {
				text-shadow:
					0.1px 0 0 $colorGreenDark,
					-0.1px 0 0 $colorGreenDark,
					0 0.1px 0 $colorGreenDark,
					0 -0.1px 0 $colorGreenDark,
					0.1px 0.1px 0 $colorGreenDark,
					-0.1px -0.1px 0 $colorGreenDark,
					-0.1px 0.1px 0 $colorGreenDark,
					0.1px -0.1px 0 $colorGreenDark;
			}

			&::after {
				opacity: 1;
			}
		}
	}

	&__no {
		display: flex;
		justify-content: center;
		align-items: center;
		text-align: center;
		font-family: $secFont;
		font-size: rem(14);
		color: #fff;
		width: 100%;
		height: rem(33);
		border-radius: rem(12);
		border: 1px solid #4caf50;
		background-color: #4caf50;
		transition: background-color 0.2s ease, color 0.2s ease;

		&:hover {
			background-color: $colorGreenDark;
		}
	}
}

.price-cart {
	display: flex;
	align-items: center;
	flex-shrink: 0;
	gap: rem(30);


	@media(min-width: 1280px) {
		gap: rem(57);
	}

	@media(max-width: 991px) {
		width: 100%;
		margin: rem(26) 0 0;
		gap: rem(5);
		justify-content: space-between;

		> * {
			flex-shrink: 0;
		}

		.product-count {
			@media(max-width: 991px) {
			    transform: translateX(11%);
			}
		}
	}

	&__left {
		display: flex;
		align-self: center;
		text-align: right;
		position: relative;
	}

	&__old {
		font-size: rem(12);
		line-height: 1;
		font-weight: 300;
		color: $colorBlueDark;
		text-decoration: line-through;
		position: absolute;
		bottom: calc(100% + 3px);
		right: 0;
		z-index: 1;
	}

	&__current {
		font-family: $secFont;
		font-size: rem(16);
		line-height: 1;

		&--brown {
			color: #59060f;
		}

		&--blue {
			color: $colorBlueDark;

			@media(max-width: 991px) {
				min-width: rem(100);
				text-align: right;
			}

		}
	}
}

.add-success {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	gap: rem(10);
	padding: rem(20) rem(16);
	background-color: #9fd7bd;

	&__icon {
		width: rem(36);
		height: rem(36);

		img {
			width: 100%;
			height: 100%;
		}
	}

	&__text {
		font-size: rem(14);
		color: $colorBlueDark;
	}
}

.error-basket {
	display: flex;
	align-items: center;
	justify-content: center;
	width: 100%;
	gap: rem(10);
	padding: rem(20) rem(16);
	background-color: #fde3df;
	margin: rem(10) 0 0;
	border-radius: rem(16);

	&__icon {
		width: rem(36);
		height: rem(36);

		img {
			width: 100%;
			height: 100%;
		}
	}

	&__text {
		width: 100%;
		max-width: rem(494);
		font-size: rem(12);
		text-align: center;
		color: #ab0719;
	}
}

.login-popup {
	width: 100%;
	position: relative;
	max-width: rem(685);
	margin: 0 auto;
	background-color: #fff;

	@media(max-width: 744px) {
		height: 100%;
	}

	&__body {
		width: 100%;
		padding: rem(38) rem(16) rem(44);
	}


}

.reg-form {
	&__title {
		font-family: $secFont;
		font-size: rem(18);
		color: $colorBlueDark;
		text-align: center;
		padding: 0 rem(10);
		margin: 0 0 rem(18);
	}

	&__form {
		max-width: rem(328);
		margin: 0 auto;

		.input-block + .input-block {
			margin: rem(25) 0 0;
		}

		.error-form + .input-block {
			margin: rem(25) 0 0;
		}

		.mid-btn {
			width: 100%;
			height: rem(44);
			margin: rem(25) 0 0;
		}
	}

	&__log {
		display: flex;
		justify-content: center;
		gap: rem(12);
	}

	.simple-link {
		font-size: rem(14);
	}

	&__bottom {

		.reg-form__title {
			margin: rem(23) 0 rem(14);
		}

	}

	&__links {
		display: flex;
		flex-direction: column;
		align-items: center;
		text-align: center;
		gap: rem(10);
		margin: rem(24) 0;

		&--sec {
			flex-direction: row;
			gap: rem(5);
			justify-content: center;

			.simple-link:last-child {
				text-transform: uppercase;
			}
		}

		a {
			font-weight: 400;
		}
	}
}

.close-popup {
	position: absolute;
	top: rem(17);
	right: rem(18);
	z-index: 3;
	width: rem(15);
	height: rem(15);
	color: $colorBlueDark;
	transition: color 0.2s ease;

	&::after {
		content: '';
		display: block;
		position: absolute;
		top: 50%;
		left: 50%;
		transform: translate(-50%, -50%);
		width: 180%;
		height: 180%;
		z-index: 1;
		opacity: 0;
	}

	svg {
		width: 100%;
		height: 100%;
	}

	&:hover {
		color: $colorBlueLight;
	}

	&:focus-visible {
		outline: none;
	}
}

.input-block {
	width: 100%;

	&__title {
		display: block;
		margin: 0 0 rem(6);
		font-size: rem(14);
		color: $colorBlueDark;
		font-weight: 400;
	}

	&__inp {
		position: relative;
	}

	&__ps {
		position: absolute;
		top: 50%;
		right: 20px;
		z-index: 1;
		transform: translateY(-50%);
		width: rem(15);
		height: rem(15);
		color: $colorBlueDark;
		transition: color 0.2s ease;

		&::before {
			content: '';
			display: block;
			position: absolute;
			top: 50%;
			left: 50%;
			transform: translate(-50%, -50%);
			width: rem(46);
			height: rem(46);
			z-index: 1;
			opacity: 0;
		}

		&::after {
			content: '';
			display: block;
			position: absolute;
			width: 19px;
			height: 1px;
			background-color: $colorBlueDark;
			top: 51%;
			left: 50%;
			z-index: 1;
			transform: translate(-45%, -50%) rotate(45deg);
			transition: background-color 0.2s ease;
		}

		&:hover {
			color: $colorBlueLight;

			&::after {
				background-color: $colorBlueLight;
			}
		}

		svg {
			width: 100%;
			height: 100%;
		}

		&.show {
			&::after {
				display: none;
			}
		}
	}

	input {
		width: 100%;
		height: rem(42);
		border-radius: rem(16);
		border: 1px solid #202454;
		text-align: center;
		font-family: $mainFont;
		font-size: rem(14);
		padding: 0 rem(47);
		color: #737488;
		transition: border-color 0.2s ease;

		&.error {
			border-color: #ab0719;
		}

		&::placeholder {
			font-family: inherit;
			font-size: inherit;
			color: inherit;
			transition: opacity 0.2s ease;
		}

		&:focus {
			&::placeholder {
				opacity: 0;
			}
		}
	}
}

.error-form {
	font-size: rem(12);
	font-weight: 400;
	text-align: center;
	color: #ab0719;
	margin: rem(10) 0 0;
	display: none;
}
