.close-popup {
  position: absolute;
  top: 1.0625rem;
  right: 1.125rem;
  z-index: 3;
  width: 0.9375rem;
  height: 0.9375rem;
  color: #202454;
  transition: color 0.2s ease;
}
.close-popup::after {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 180%;
  height: 180%;
  z-index: 1;
  opacity: 0;
}
.close-popup svg {
  width: 100%;
  height: 100%;
}
.close-popup:hover {
  color: #4abbf3;
}
.close-popup:focus-visible {
  outline: none;
}

.input-block {
  width: 100%;
}
.input-block__title {
  display: block;
  margin: 0 0 0.375rem;
  font-size: 0.875rem;
  color: #202454;
  font-weight: 400;
}
.input-block__inp {
  position: relative;
}
.input-block__ps {
  position: absolute;
  top: 50%;
  right: 20px;
  z-index: 1;
  transform: translateY(-50%);
  width: 0.9375rem;
  height: 0.9375rem;
  color: #202454;
  transition: color 0.2s ease;
}
.input-block__ps::before {
  content: "";
  display: block;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 2.875rem;
  height: 2.875rem;
  z-index: 1;
  opacity: 0;
}
.input-block__ps::after {
  content: "";
  display: block;
  position: absolute;
  width: 19px;
  height: 1px;
  background-color: #202454;
  top: 51%;
  left: 50%;
  z-index: 1;
  transform: translate(-45%, -50%) rotate(45deg);
  transition: background-color 0.2s ease;
}
.input-block__ps:hover {
  color: #4abbf3;
}
.input-block__ps:hover::after {
  background-color: #4abbf3;
}
.input-block__ps svg {
  width: 100%;
  height: 100%;
}
.input-block__ps.show::after {
  display: none;
}
.input-block input {
  width: 100%;
  height: 2.625rem;
  border-radius: 1rem;
  border: 1px solid #202454;
  text-align: center;
  font-family: "Montserrat", Verdana, Arial, sans-serif;
  font-size: 0.875rem;
  padding: 0 2.9375rem;
  color: #737488;
  transition: border-color 0.2s ease;
}
.input-block input.error {
  border-color: #ab0719;
}
.input-block input::placeholder {
  font-family: inherit;
  font-size: inherit;
  color: inherit;
  transition: opacity 0.2s ease;
}
.input-block input:focus::placeholder {
  opacity: 0;
}

.error-form {
  font-size: 0.75rem;
  font-weight: 400;
  text-align: center;
  color: #ab0719;
  margin: 0.625rem 0 0;
  display: none;
}